'use strict'

const Uuid = require('uuid4')

enum EventType {
  undefined = "undefined",
  log = "log",
  audit = "audit",
  error = "error",
  trace = "trace",
}

abstract class EventAction {
  type: EventType = EventType.undefined
  action: string = "undefined"
}

class EventActionLog extends EventAction {
  type: EventType = EventType.log
  action: EventTypeActionLog = EventTypeActionLog.undefined
}

class EventActionAudit extends EventAction {
  type: EventType = EventType.audit
  action: EventTypeActionAudit = EventTypeActionAudit.undefined
}

class EventActionError extends EventAction {
  type: EventType = EventType.error
  action: EventTypeActionError = EventTypeActionError.undefined
}

class EventActionTrace extends EventAction {
  type: EventType = EventType.trace
  action: EventTypeActionTrace = EventTypeActionTrace.undefined
}

enum EventTypeActionLog {
  undefined = "undefined",
  info = "info",
  debug = "debug",
  verbose = "verbose",
  perf = "perf",
}

enum EventTypeActionAudit {
  undefined = "undefined",
}

enum EventTypeActionError {
  undefined = "undefined",
  internal = "internal",
  external = "external"
}

enum EventTypeActionTrace {
  undefined = "undefined",
  start = "start",
  end = "end",
}

enum EventTypeAction {
  start = "start",
  end = "end",
  info = "info",
  debug = "debug",
  verbose = "verbose",
  perf = "perf",
  internal = "internal",
  external = "external"
}

enum EventStatusType {
  success = "success",
  failed = "failed"
}

class EventTraceMetadata {
  service: string
  traceId:	string
  spanId: string
  parentSpanId?:	string
  sampled?:	number
  flags?:	number

  constructor (service: string, traceId: string, spanId: string) {
    this.service = service
    this.traceId = traceId
    this.spanId = spanId
  }
}

class EventStateMetadata {
  status: EventStatusType
  code?: number
  description?: string

  constructor ( status: EventStatusType ) {
    this.status = status
  }
}
class EventMetadata {
  id: string = Uuid()
  type: EventType
  action: string
  createdAt: string
  responseTo?: string
  state: EventStateMetadata

  constructor ( id: string, typeAction: EventAction, createdAt: string, state: EventStateMetadata ) {
    this.id = id
    this.type = typeAction.type
    this.action = typeAction.action
    this.createdAt = createdAt
    this.state = state
  }
}

class MessageMetadata {
  event: EventMetadata
  trace: EventTraceMetadata

  constructor(event: EventMetadata, trace: EventTraceMetadata) {
    this.event = event
    this.trace = trace
  }
}

class EventMessage {
  id: string = Uuid()
  from?: string
  to?: string
  pp?: string
  metadata?: MessageMetadata
  type?: string
  content?: any
}


export {
  EventMessage,
  EventType,
  EventActionLog,
  EventActionAudit,
  EventActionTrace,
  EventActionError,
  EventTypeActionLog,
  EventTypeActionAudit,
  EventTypeActionTrace,
  EventTypeActionError,
  EventStatusType,
  MessageMetadata,
  EventMetadata,
  EventStateMetadata,
  EventTraceMetadata,

}
